.accounting-service-header-1 {
    .image-banner {
        min-height: 500px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
}
.accounting-service-home-section-1 {
    .form-request {
        filter: drop-shadow(0px 30px 60px rgba(0, 0, 0, 0.06));
        margin-top: -50px;
        .form-control {
            border-color: var(--tc-system-stroke);
        }
    }
    .image-banner {
        min-height: 500px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: start;
    }

    // nav-tabs
    .nav-tabs {
        --bs-nav-tabs-border-width: var(--bs-border-width);
        --bs-nav-tabs-border-color: var(--bs-border-color);
        --bs-nav-tabs-border-radius: 0;
        --bs-nav-tabs-link-active-color: var(--tc-theme-primary);
        --bs-nav-tabs-link-hover-border-color: transparent;
        .underline {
            width: 95px;
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            opacity: 0;
            background: var(--tc-system-yellow);
            transition: 0.3s ease-in-out;
        }
    }
    .nav {
        --bs-nav-link-padding-x: 20px;
        --bs-nav-link-padding-y: 10px;
        --bs-nav-link-font-weight: var(--tc-fw-bold);
        --bs-nav-link-color: var(--tc-system-black);
        --bs-nav-link-hover-color: var(--tc-theme-primary);
    }
    .nav-link {
        display: block;
        padding: var(--bs-nav-link-padding-y) var(--bs-nav-link-padding-x);
        font-size: 14px;
        font-weight: 600;
        color: var(--bs-nav-link-color);
        &::before {
            width: 1px;
            height: 16px;
            background: var(--tc-system-stroke);
            position: absolute;
            content: '';
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
        }
        &:first-child {
            padding-left: 0 !important;
            .underline {
                right: unset;
            }
        }
        &:last-child {
            padding-right: 0 !important;
            .underline {
                left: unset;
            }
            &::before {
                display: none;
            }
        }
    }
    .nav-tabs .nav-item.show .nav-link,
    .nav-tabs .nav-link.active {
        color: var(--bs-nav-tabs-link-active-color);
        background-color: transparent;
        border-color: transparent;
        .underline {
            opacity: 1;
            transition: 0.3s ease-in-out;
        }
    }
}
.accounting-service-home-section-2,
.accounting-services-section-2 {
    .card-expertise {
        .card-expertise-link {
            margin-bottom: 2rem;
            h5 {
                opacity: 1;
                transition: all 0.3s ease-in-out;
                margin-bottom: 1rem;
            }
        }
        &::before {
            transition: all 0.2s ease-in-out;
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 100%);
            border-radius: 20px;
            opacity: 0.6;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            z-index: 0;
        }
        &:hover {
            .card-expertise-link {
                h5 {
                    opacity: 1;
                    margin-bottom: 2rem;
                }
            }
        }
        @media (max-width: 1400px) {
            .card-expertise-link {
                margin-bottom: 2rem;
                h5 {
                    margin-bottom: 2rem;
                }
            }
        }
    }
}
.cards {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: repeat(var(--cards-count), var(--card-height));
    gap: 40px 0;
    .card-custom {
        position: sticky;
        top: 0;
        border: none;
        .card__inner {
            display: flex;
            transform-origin: center top;
        }
    }
}
.accounting-service-home-section-3 {
    .card-services {
        img {
            -webkit-filter: grayscale(1);
            filter: grayscale(1);
        }
        .bg-overlay {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(0deg, #000 0%, rgba(0, 0, 0, 0) 100%);
            border-radius: 20px;
        }
        .card-services-content {
            position: absolute;
            bottom: 40px;
            left: 0;
            right: 0;
            margin: auto;
            z-index: 1;
        }
        .pattern {
            transition: all 0.8s ease-in-out;
            .ellipse-1 {
                position: absolute;
                width: 328px;
                height: 328px;
                border-radius: 328px;
                background: var(--tc-system-radish);
            }
            .ellipse-2 {
                position: absolute;
                width: 92px;
                height: 92px;
                border-radius: 92px;
                background: var(--tc-system-radish);
                opacity: 0.1;
            }
            .ellipse-3 {
                position: absolute;
                width: 180px;
                height: 180px;
                border-radius: 180px;
                background: var(--tc-system-radish);
                opacity: 0.2;
            }
            .ellipse-4 {
                position: absolute;
                width: 198px;
                height: 198px;
                border-radius: 198px;
                background: var(--tc-system-yellow);
            }
            &.bg-1 {
                .ellipse-1 {
                    left: -168px;
                    top: -40px;
                }
                .ellipse-2 {
                    left: 364px;
                    top: -23px;
                }
                .ellipse-3 {
                    left: 325px;
                    bottom: -130px;
                }
                .ellipse-4 {
                    right: -90px;
                    top: 176px;
                }
            }
            &.bg-2 {
                .ellipse-1 {
                    left: 48px;
                    top: -165px;
                }
                .ellipse-2 {
                    right: 84px;
                    top: -28px;
                }
                .ellipse-3 {
                    left: 91px;
                    bottom: -127px;
                }
                .ellipse-4 {
                    right: 100px;
                    bottom: -100px;
                }
            }
            &.bg-3 {
                .ellipse-1 {
                    right: -102px;
                    top: -129px;
                }
                .ellipse-2 {
                    left: -31px;
                    top: 211px;
                }
                .ellipse-3 {
                    right: 205px;
                    bottom: -90px;
                }
                .ellipse-4 {
                    left: 0px;
                    bottom: -78px;
                }
            }
        }
        &:hover {
            .pattern {
                transform: translateX(-100%);
            }
        }
        @media (max-width: 1400px) {
            .pattern {
                transform: translateX(-100%);
            }
        }
    }
}
.accounting-service-home-section-4,
.accounting-service-about-section-4 {
    .ellipse-1 {
        width: 320px;
        height: 320px;
        border-radius: 320px;
        opacity: 0.1;
        background: var(--tc-system-black);
    }
    .ellipse-2 {
        width: 482px;
        height: 482px;
        border-radius: 482px;
        z-index: -1;
        border: 1px solid var(--tc-system-yellow);
    }
    .avatar-2 {
        margin-left: -1rem;
    }
}
.accounting-service-home-section-5 {
    .card-team {
        .card-link {
            transition-duration: 0.3s;
            margin-top: 30px;
            .icon {
                svg {
                    height: 14px;
                    width: 14px;
                    fill: var(--tc-body-text-secondary);
                    opacity: 0.5;
                }
                &:hover {
                    svg {
                        fill: var(--tc-theme-primary);
                        opacity: 1;
                        transition-duration: 0.3s;
                    }
                }
            }
        }
    }
    @media (min-width: 1400px) {
        .card-team {
            .card-link {
                opacity: 0;
                margin-top: 30px;
            }
            &:hover {
                .card-link {
                    opacity: 1;
                    transform: translateY(5px);
                }
            }
        }
    }
}
.accounting-service-about-section-2 {
    .card-service {
        background: #eceff1;
        border-color: #eceff1;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: var(--tc-system-white);
            border-color: var(--tc-theme-primary) !important;
        }
    }
}
.accounting-service-about-section-3 {
    .quote {
        background: linear-gradient(180deg, var(--tc-theme-primary) 0%, #fff 100%);
    }
}
.accounting-service-about-section-4 {
    // accordion
    .accordion {
        .card {
            background: transparent;
            transition: all 0.3s linear;
            .card-header {
                background: transparent;
                a {
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    background-color: #eceff1;
                    transition: all 0.3s ease-in-out;
                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: remixicon;
                            content: '\f1af';
                            color: var(--tc-theme-primary);
                            font-size: 14px;
                            font-weight: 300;
                        }
                    }
                    &.collapsed {
                        background-color: transparent;
                        .arrow {
                            background-color: transparent;
                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-black);
                                transform: rotate(180deg);
                                content: '\ea13';
                            }
                        }
                    }
                }
            }
            .collapse {
                background: linear-gradient(180deg, #eceff1 0%, #fff 113.64%);
                transition: all 0.3s linear;
                &.show {
                    border-bottom: 0;
                }
            }
            .collapsing {
                background: linear-gradient(180deg, #eceff1 0%, #fff 113.64%) !important;
                transition: all 0.3s linear;
            }
        }
    }
}
.input-group {
    .form-control {
        color: #fff;
    }
}
.accounting-blog-details-section-2 {
    .input-group {
        .form-control {
