}
// accordion
.card {
    transition: all 0.3s ease-in-out;
    .card-header {
        a {
            .arrow {
                &::before {
                    transition: 0.3s;
                    transform: rotate(0deg);
                    font-family: bootstrap-icons;
                    content: '\F2EA';
                    color: var(--tc-system-navy-blue-3);
                    font-size: 20px;
                }
            }
            border-bottom: 1px solid transparent;
            &.collapsed {
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                .arrow {
                    background-color: transparent;
                    &::before {
                        transition: 0.3s;
                        color: var(--tc-system-navy-blue-3);
                        transform: rotate(180deg);
                        content: '\F4FE';
                    }
                }
            }
        }
    }
}
.tag-recommended {
    display: flex;
    width: 136px;
    height: 28px;
    top: 0;
    left: 164px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 0px 0px 10px 10px;
    background: var(--tc-theme-primary);
}
.section-portfolio-2.accordion {
    justify-content: center;
    align-content: center;
    position: relative;
    top: 0;
    left: 5vw;
    transition: all 200ms ease-in-out 50ms;
    min-height: 50vh;
    width: 90vw;
    opacity: 0;
    .accordion-item {
        margin-left: 15px;
        margin-right: 15px;
        border-radius: 10px !important;
        border: none;
        background: none;
        cursor: pointer;
        display: flex;
        .accordion-link {
            position: relative;
            background-color: white;
            transition: all 0.2s linear;
            .accordion-link-item {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: start;
                gap: 12px;
            }
        }
        .accordion-img {
            height: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            border-top-right-radius: 12px;
            border-bottom-right-radius: 12px;
        }
    }
    .default {
        height: 300px;
        width: 300px;
        overflow: hidden;
        .accordion-link {
            width: 0;
            .accordion-link-item {
                height: 0;
                opacity: 0;
            }
        }
        .accordion-img {
            width: 100%;
        }
    }
    .expand {
        height: 300px;
        width: 630px;
        .accordion-link {
            width: 50%;
            padding: 30px;
            .accordion-link-item {
                transition: all 0.5s ease-in-out;
                height: 100%;
                opacity: 1;
            }
        }
        .accordion-img {
            width: 50%;
        }
    }
    @media (max-width: 991px) {
        .default {
            height: 230px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 300px;
            width: 100%;
            .accordion-link {
                width: 50%;
                padding: 30px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;

                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 50%;
            }
        }
    }
    @media (max-width: 767.98px) {
        .accordion-item {
            h2 {
                font-size: 24px !important;
            }
            p {
                font-size: 12px !important;
            }
        }
        .default {
            height: 400px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 400px;
            width: 100%;
            .accordion-link {
                width: 100%;
                padding: 20px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;
                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
    }
}
.progress,
.progress-stacked {
    --bs-progress-height: 4px;
    --bs-progress-font-size: 0.75rem;
    --bs-progress-bg: #e9e9e9;
    --bs-progress-border-radius: 0px;
    --bs-progress-box-shadow: var(--bs-box-shadow-inset);
    --bs-progress-bar-color: #fff;
    --bs-progress-bar-bg: var(--tc-theme-primary);
    --bs-progress-bar-transition: width 0.6s ease;
    display: flex;
    height: var(--bs-progress-height);
    overflow: hidden;
    font-size: var(--bs-progress-font-size);
    background-color: var(--bs-progress-bg);
    border-radius: var(--bs-progress-border-radius);
}
.tag-process {
    .icon-shape {
        transform: rotate(90deg);
    }
    @media (min-width: 1400px) {
        .icon-shape {
            transform: rotate(0deg);
        }
    }
}
.banner-ads {
    position: relative;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(0, 81, 83, 0) 0%, var(--tc-theme-primary) 100%);
    }
}
.pagination_item {
    border-radius: 10px !important;
    font-size: 14px !important;
    font-weight: 400 !important;
    &:hover {
        background: var(--tc-system-yellow);
        color: var(--tc-system-black) !important;
    }
    &:focus {
        background-color: var(--tc-system-yellow) !important;
        color: var(--tc-system-black) !important;
    }
    &.active {
        background-color: var(--tc-system-yellow) !important;
        color: var(--tc-system-black) !important;
    }
}
